body {
  background-color: #D2A0E6;
}

img {
  object-fit: contain;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

picture {
  font-size: 0;
}

.container {
  max-width: 1185px;
  margin: 0 auto;
  padding: 0 15px;
  overflow: hidden;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.is-visible {
  opacity: 1;
}

.section-title {
  font-weight: 700;
  font-size: 48px;
  line-height: 55px;
  color: #fff;
}
.section-title span {
  color: #000046;
}

.header {
  padding-top: 100px;
  position: relative;
  z-index: 1;
}
.header .header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .header-container__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}
.header .header-container__item {
  font-weight: 600;
  font-size: 20px;
  line-height: 31px;
  color: #000;
  border-bottom: 1px solid #000;
}
.header .header-container__item:not(:last-child) {
  margin-right: 15px;
}

.hero {
  position: relative;
  padding-top: 35px;
}
.hero .hero-container {
  margin-bottom: 40px;
  display: flex;
}
.hero .hero-left {
  display: flex;
  flex-direction: column;
  max-width: 502px;
  z-index: 1;
}
.hero .hero-left__title {
  margin-bottom: 12px;
  font-weight: 400;
  font-size: 62px;
  line-height: 68px;
  color: #fff;
}
.hero .hero-left__title span {
  font-weight: 700;
  font-size: 85px;
  color: #FF1414;
}
.hero .hero-left__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 12px;
}
.hero .hero-left__item {
  display: flex;
  align-items: center;
}
.hero .hero-left__item span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 75px;
  height: 69px;
  font-weight: 800;
  font-size: 58px;
  line-height: 90px;
  background-color: #D1F3FF;
  box-shadow: 0px 1px 15px 7px rgba(163, 163, 163, 0.3);
  border-radius: 24px;
  color: #FF1414;
  z-index: 2;
}
.hero .hero-left__item:not(:last-child) {
  margin-bottom: 22px;
}
.hero .hero-left__content {
  padding: 22px;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 24px;
  color: #000;
  background-color: #D1F3FF;
  border-radius: 24px;
  z-index: 1;
}
.hero .hero-left__content div {
  margin-right: 10px;
}
.hero .hero-left__content span {
  margin-right: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 57px;
  height: 57px;
  background: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
}
.hero .hero-left .capsuls {
  padding: 6px 20px;
}
.hero .hero-left .capsuls__first {
  transform: translateY(6px);
}
.hero .hero-left .capsuls__second {
  transform: translateX(-7px);
}
.hero .hero-right {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero .hero-right::before {
  content: "";
  position: absolute;
  top: -130px;
  width: 925px;
  height: 417px;
  background: linear-gradient(180deg, #D2A0E6 42.62%, rgba(210, 160, 230, 0) 100%);
}
.hero .hero-right__img {
  position: absolute;
  top: -140px;
